<template>
  <div class="card border-0 h-100">
    <div class="card-body d-flex flex-column">
      <h5
        class="card-title mb-2 text-body-secondary d-flex justify-content-between"
      >
        <div>
          {{ $t('orderView.orderStatus') }}：<span
            class="fst-italic text-secondary"
            v-if="tempData.mealStatus === 25"
          >
            {{ $t('orderView.payment') }}</span
          >
          <span
            class="fst-italic text-secondary"
            v-else-if="tempData.mealStatus === 50"
            >{{ $t('orderView.diningOut') }}</span
          >
          <span class="fst-italic text-secondary" v-else>{{
            $t('orderView.finish')
          }}</span>
        </div>
        <button
          @click.prevent="cancleOrder(tempData._id)"
          class="card-link h6 text-decoration-underline border-0 bg-body"
          :class="{
            'text-decoration-line-through': tempData.mealStatus !== 25,
          }"
          :disabled="tempData.mealStatus !== 25"
        >
          {{ $t('orderView.cancleOrder') }}
        </button>
      </h5>
      <h6>
        {{ $t('shoppingOptions.paymethod') }}：<span
          class="fst-italic text-secondary"
          >{{ $t(payMethod) }}</span
        >
      </h6>
      <div
        class="progress mb-3"
        role="progressbar"
        aria-label="Animated striped example"
        aria-valuenow="75"
        aria-valuemin="0"
        aria-valuemax="100"
      >
        <div
          class="progress-bar progress-bar-striped progress-bar-animated"
          :style="`width: ${tempData.mealStatus}%`"
        ></div>
      </div>
      <div class="card-text mb-3">
        <ol>
          <template v-for="(item, index) in tempData.productList" :key="index">
            <li class="border-bottom mb-3">
              <div clas="w-100">
                <h5
                  class="mb-0 d-flex justify-content-between align-items-center w-100"
                  :class="{
                    'text-decoration-line-through': tempData.mealStatus === 100,
                  }"
                >
                  <div>
                    {{ $t(item.flavor) }} - ${{ item.price }} X
                    {{ item.quantity }}
                  </div>
                  <div>${{ item.price * item.quantity }}</div>
                </h5>
                <p
                  v-if="item.type === 'drink' || item.type === 'coffee'"
                  class="mb-0 text-dark-emphasis"
                >
                  {{ $t('orderView.size') }}:<span
                    class="fst-italic text-secondary"
                    >{{ item.size }}</span
                  >, {{ $t('orderView.ice') }}:<span
                    class="fst-italic text-secondary"
                    >{{ $t(iceList[item.ice].title) }}</span
                  >, {{ $t('orderView.sugar') }}:<span
                    class="fst-italic text-secondary"
                    >{{ $t(sugarList[item.sugar].title) }}</span
                  >
                </p>
              </div>
            </li>
          </template>
        </ol>
        <div class="d-flex justify-content-between">
          <h4 class="fw-bold">{{ $t('orderView.total') }}：</h4>
          <h4 class="fw-bold">${{ $toCurrency(amount) }}</h4>
        </div>
      </div>
      <div class="card-footer">
        <p v-if="tempData.mealStatus === 25">
          {{ $t('orderView.orderMessage.notPaidYet') }}
          <!-- 付款去按鈕 -->
          <a
            href="#"
            @click.prevent="createLinePay(tempData._id)"
            v-if="tempData.payMethod === 'shoppingOptions.paymethods.linePay'"
          >
            <component :is="'wired-button'" class="bg-dark text-light h4">
              <i class="bi bi-caret-right-fill text-light px-2"></i
              >{{ $t('shoppingOptions.checkout') }}
            </component>
          </a>
        </p>
        <p v-else-if="tempData.mealStatus === 50">
          {{ $t('orderView.orderMessage.eatingOut') }}
        </p>
        <p v-else-if="tempData.mealStatus === 100">
          {{ $t('orderView.orderMessage.finishedEating') }}
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import Cookies from 'js-cookie';
import iceOption from '@/assets/json/iceOption.json';
import sugarOption from '@/assets/json/sugarOption.json';
import { apiDeleteOrder, apiPostLinepay } from '@/assets/javascript/api';

export default {
  data() {
    return {
      payMethod: 'shoppingOptions.paymethods.cash',
      cookieList: [],
      tempData: {},
      iceList: iceOption,
      sugarList: sugarOption,
      amount: 0,
      tempCookie: '',
    };
  },
  methods: {
    cancleOrder(id) {
      if (this.tempData.mealStatus === 25) {
        apiDeleteOrder(id)
          .then(() => {
            const orderNum = this.cookieList.findIndex(
              (element) => element.orderId === id,
            );
            this.cookieList.splice(orderNum, 1);
            // 檢查cookieList是否還有order
            if (this.cookieList.length === 0) {
              Cookies.remove('luluBakeOrder');
            } else {
              this.tempCookie = '';
              this.cookieList.reverse();
              this.cookieList.forEach((item) => {
                this.tempCookie += `${JSON.stringify(item)};`;
              });
              this.tempCookie = this.tempCookie.replace(/.$/, '');
              Cookies.set('luluBakeOrder', this.tempCookie);
              this.cookieList.reverse();
              this.$emitter.emit('getCookieOrder');
              this.$router.push(`/order/${this.cookieList[0].cookieId}`);
              this.$emitter.emit('getOrder', this.cookieList[0].orderId);
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    createLinePay(id) {
      apiPostLinepay(id)
        .then((res) => {
          // window.open(res.data.data.web, '_blank');
          window.location.href = res.data.data.web;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    this.$emitter.on('getOrderData', (e) => {
      this.tempData = e;
      this.payMethod = e.payMethod;
      this.amount = e.amount;
    });
    if (Cookies.get('luluBakeOrder')) {
      const tempCookie = Cookies.get('luluBakeOrder').split(';');
      Object.values(tempCookie).forEach((item) => {
        this.cookieList.push(JSON.parse(item));
      });
      this.cookieList.reverse();
    }
  },
};
</script>
